html {
  min-height: 100%;
  overflow: scroll; }

body {
  font-family: 'Times New Roman', Times, serif;
  font-size: 16px;
  line-height: 1.2;
  color: #1d3350; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

a {
  font-size: 100%;
  color: inherit;
  text-decoration: none;
  transition: color 0.4s ease-in-out; }
  a:hover {
    color: inherit;
    text-decoration: none;
    color: #72e6c7; }

.btn {
  padding: 5px;
  border-radius: 5px;
  border: none;
  background: none;
  color: inherit;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  transition: background 0.4s ease-in-out; }
  .btn:hover {
    background: #72e6c7; }
  .btn:active {
    color: #b2eff6; }
